<template>
  <div class="login">
    <Header :activeTopButton="-1" />
    <div class="credentials-body login-body">
      <div class="credentials-body-wrapper login-body-wrapper">
        <div class="left">
          <!-- <img class="img-bg" height="1288" src="@/assets/image/credentials/login-bg.png" alt=""> -->
          <div class="qr-code">
            <img height="282" width="282" src="" alt="">
            <div>关注戒友吧</div>
            <div>WAP.YUMING.COM</div>
            <div>不经一番寒彻骨，怎得梅花扑鼻香</div>
          </div>
        </div>
        <div class="right">
          <div class="title">
            欢迎回到戒友大家庭
          </div>
          <Form ref="formInline" :model="formInline" :rules="ruleInline">
              <FormItem prop="user" label="用户名：" :label-width="120">
                  <Input type="text" v-model="formInline.user" placeholder="请输入您的用户名" />
              </FormItem>
              <FormItem prop="password" label="密码：" :label-width="120">
                  <Input type="password" v-model="formInline.password" placeholder="请输入账户密码"/>
              </FormItem>
              <FormItem prop="code" label="验证码：" :label-width="120">
                  <Input type="text" v-model="formInline.code" placeholder="请输入图形验证码" />
              </FormItem>
              <FormItem>
                <div class="code-img-con">
                  <img height="31" width="101" src="" alt="">
                  <span>换一个</span>
                </div>
              </FormItem>
              <FormItem class="btn-con">
                  <Button type="primary" @click="login()">立即注册</Button>
              </FormItem>
          </Form>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/common/Header.vue'
import Footer from '@/components/common/Footer.vue'

export default {
  name: 'Login',
  components: {
    Header,
    Footer
  },
  data() {
    return {
      formInline: {
        user: '',
        password: '',
        code: '',
      },
      ruleInline: {
        user: [
          { required: true, type: 'string', min: 3, max: 15, message: '用户名由3-15个字符组成', trigger: 'blur' }
        ],
        password: [
          { required: true, type: 'string', min: 6, max: 15, message: '密码包含3-15个字符', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请输入图形验证码', trigger: 'blur' }
        ],
      }
    };
  },
  methods: {
    login() {
      this.$refs.formInline.validate((valid) => {
        if (valid) {
          this.$store.dispatch("setLoginStatus", true);
          this.$router.push({ name: 'Home' });
        }
      })
    },
  },
  mounted() {},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
@import "../../assets/css/vars.less";
@import "../../assets/css/login.less";
</style>